import React from "react";
import TooltipFromHtml from "eshop/modules/core/components/ui/TooltipFromHtml";
import {OraLoader} from "eshop/components/OraCommonComponents";
import OnlineUtils from "eshop/utils/OnlineUtils";
import {Sticker} from "../../lp/offers/Sticker";
import {DetailsButtons} from "eshop/modules/configurator/components/lp/offers/newOfferBoxComponents/DetailsButtons";
import OraHtmlText from "../../../../core/components/ui/OraHtmlText";
import VasPackages from "eshop/modules/configurator/components/lp/offers/VasPackages";


export class MobilePropositionB2B extends React.PureComponent {
    constructor(props) {
        super(props);
        this.onClick = this.onClick.bind(this);
    }

    onClick(e) {
        console.log("PROPOSITION");
        this.props.selectOfferCallback(this.props.offer.id);
    }

    price() {
        return OnlineUtils.getPaymentsForRole(this.props.offer.payments, this.props.contractRole);
    }

    oldPrice() {
        return OnlineUtils.getPaymentsForRole(this.props.offer.payments, "");
    }

    render() {
        let priceObj = this.price();
        let oldPriceObj = this.oldPrice();
        return (
            <div className={"opl-carousel__item opl-packet-box l-col l-col-3 u-margin-l u-large-margin-xl slick-slide slick-active"+ (this.props.selected ? " is-selected" : " ")}>
                <CarouselItem
                    key={"carouselItem_" + this.props.offer.id + "_" + this.props.index}
                    index={this.props.index}
                    selected={this.props.selected}
                    onClick={this.onClick}
                    showNet={this.props.showNet}
                    isB2B={this.props.isB2B}
                    priceObj={priceObj}
                    oldPriceObj={oldPriceObj}
                    customPrice={this.props.contractRole}
                    offer={this.props.offer}
                    tooltipValues={getTooltipValues(this.props.descriptions)}
                    renderMobileSticker= {this.props.offer.sticker !== null && Array.isArray(this.props.offer.sticker.products) &&
                        this.props.offer.sticker.products.length === 0 ? () =>
                        <Sticker
                            sticker={this.props.offer.sticker}
                            renderMobileVersion={false}/> : null}>
                    <ChooseButton/>
                </CarouselItem>
            </div>
        );
    }
}
let counter = 0;
const CarouselItem = (props = {}) => {
    return (
        <div
            className={"u-position-relative u-cursor-pointer "}
            onClick={props.onClick}>

            <div className="opl-selection-layer" aria-hidden="true">
                <div className="opl-selection-layer__border"/>
                <div className="opl-selection-layer__checkmark"/>
            </div>
            <div className={"g-white1-bgc u-padding-all-l u-box-shadow--s"}>
            <div className="u-animate-height js-layout-fixer-group-2">
                <p className="u-margin-m js-height-sensitive-element">{props.offer.rateplanName}</p>
            </div>
            {props.renderMobileSticker &&
            <div className={"u-position-absolute u-position-right u-padding-right u-padding-top u-position-top"}>
                    {props.renderMobileSticker()}
            </div>
            }
            <div className="u-animate-height js-layout-fixer-group-3">
                <div className="u-margin-m js-height-sensitive-element">
                    <PriceTag
                        priceObj={props.priceObj}
                        oldPriceObj={props.oldPriceObj}
                        isB2B={props.isB2B}
                        showNet={props.showNet}
                        offer={props.offer}
                        customPrice={props.customPrice}
                        tooltipValues={props.tooltipValues}
                        renderMobileSticker={props.renderMobileSticker}/>

                </div>
            </div>

            <div className="o-separator u-margin-l"/>
            <div className="u-animate-height js-layout-fixer-group-4">
                <div className="js-height-sensitive-element u-padding-xl">

                    <ClassificationAttributesBox {...props}/>
                </div>
            </div>
            <div className="u-animate-height js-layout-fixer-group-5">
                <div className="js-height-sensitive-element">
                    <VasPackages {...props} />
                </div>
            </div>

            <div className="u-animate-height js-layout-fixer-group-6">
                <div className="js-height-sensitive-element">
                    <DetailsButtons
                        key={"detailsButton_" + counter++} // counter needed for details modals to work for B2B carousel - quick fix for carousel stopping all modules
                        index={props.index}
                        proposition={props.offer}
                        descriptions={props.offer.descriptions}
                        clientContext={props.clientContext}
                        contractRole={props.customPrice}/>
                </div>
            </div>

            {props.children}
            </div>
        </div>
    );
};
const ChooseButton = () => {
    return (
        <label className="u-w-100 u-large-hide u-medium-hide" htmlFor="love1">
            <div className="u-acc-txt--show">
                <button className="opl-btn o-btn u-block u-w-100 ">
                    Wybierz
                </button>
            </div>
            <div className="u-acc-txt--hide">
                <button
                    className="opl-btn o-btn u-block u-w-100 g-white1-bg g-white1-bdrc g-brand2-c"
                    disabled="disabled">
                    Wybrany
                </button>
            </div>
        </label>
    );
};
const getTooltipValues = desc => ({
    noConvergent: desc["tooltip.priceDiscount"] || "<div id=\"TOOLTIP-ID-PLACEHOLDER\"><span class=\"o-tooltip__trigger g-icon g-icon--hint g-icon--xs-s u-spacing-left-s u-cursor-pointer\"/><span class=\"o-tooltip__content o-tooltip__content--focusable\">def: Brak treści dla tooltip.priceDiscount</span></div>",
    convergent: desc["tooltip.convergentPriceDiscount"] || "<div id=\"TOOLTIP-ID-PLACEHOLDER\"><span class=\"o-tooltip__trigger g-icon g-icon--hint g-icon--xs-s u-spacing-left-s u-cursor-pointer\"/><span class=\"o-tooltip__content o-tooltip__content--focusable\">def: Brak treści dla tooltip.convergentPriceDiscount</span></div>",
    discountText: desc["discount.text"] || "<div class=\"l-row u-padding-top-s\" style=\"margin-bottom: -15px \"><p class=\"u-margin-left-m u-small-margin u-font-bold h4\">z rabatami</p></div>",
});
const PriceTag = (props = {}) => {
    let price = props.priceObj && (props.showNet ? props.priceObj.basePrice.netPrice : props.priceObj.basePrice.originalGrossPrice);
    let priceSplit = OnlineUtils.formatPrice(price).split(",");

    let oldPriceStr = props.oldPriceObj && props.oldPriceObj.basePrice && props.priceObj.basePrice &&
        (Number(props.oldPriceObj.basePrice.netPrice.replace(",", ".")) > Number(props.priceObj.basePrice.netPrice.replace(",", "."))) &&
        (props.showNet ? (OnlineUtils.formatPrice(props.oldPriceObj.basePrice.netPrice)) : (OnlineUtils.formatPrice(props.oldPriceObj.basePrice.originalGrossPrice)));

    return (
        <div className="u-w-100">
                    <div className="l-row">
                        {props.isB2B && props.customPrice && <p className="u-margin-l u-margin-left-m u-font-normal">Cena dla Ciebie</p>}
                        <div style={{zIndex: 2}}
                            className={props.isB2B ?
                                "l-col l-col-12 u-padding-l u-large-no-padding-right u-large-padding-left" :
                                "l-col l-col-12 u-padding-l u-no-padding-right u-no-padding-bottom u-padding-left"
                            }>
                            <div className="u-clearfix">
                                <div className="u-right u-small-right u-text-left u-text-nowrap"
                                     style={{minWidth: "100%"}}>
                                    <div className="u-table u-margin-minus-right-l u-small-margin-minus-right">
                                        <div className="u-table-cell">
                                                <span className="opl-price-v2 opl-medium-price-v2--l"
                                                      data-price={props.offer.discountedCommitment}>
                                                        <span className="opl-price-v2__part">
                                                            <span className="opl-price-v2__whole">{priceSplit[0]}</span>
                                                        </span>
                                                        <span className="opl-price-v2__part">
                                                            <span className="opl-price-v2__separator">,</span>
                                                            <span
                                                                className="opl-price-v2__decimal">{priceSplit[1]}</span>
                                                            <span
                                                                className="opl-price-v2__suffix u-padding-s">zł/mies.{props.showNet && " + VAT"}</span>
                                                        </span>
                                                </span>
                                            {!props.isB2B &&
                                            <OraHtmlText>{props.tooltipValues && props.tooltipValues.discountText}</OraHtmlText>
                                            }
                                            {oldPriceStr &&
                                            <div className="l-row u-padding-top-l">
                                                <div className="u-margin-left-m u-small-margin">
                                                    <p className="u-font-bold g-gray5-c u-text-line-through">{oldPriceStr} zł/mies.{props.showNet && " + VAT"}</p>
                                                </div>
                                            </div>
                                            }
                                        </div>
                                        <div className="u-table-cell u-vertical-top">
                                            {
                                                oldPriceStr && props.tooltipValues ?
                                                    TooltipFromHtml.conditionalRender(props.tooltipValues["convergent"]) :
                                                    TooltipFromHtml.conditionalRender(props.tooltipValues["noConvergent"])
                                            }
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                    <div className="l-row">
                        <div className="l-col l-col-3">
                            <OraLoader
                                loading={props.getContractRoleInProgress}
                                size="m"
                                id={"contract-role-loader-" + props.offer.id.replace("$", "-")}
                                parentId="offerb2b-filter-loader"/>
                        </div>
                    </div>

        </div>
    );
};

const ClassificationAttributesBox = props => {
    return (
        <div>
              {
                    props.offer.features.featureGroups.boxFeatures.map((feature, index, array) =>
                        <Feature
                            key={feature.code + "_" + props.offer.id + "_" + index}
                            removeClassFromLast={true}
                            feature={feature}
                            className="u-padding-l"
                            length={array.length}
                            index={index}
                            processType={props.offer.processType}
                            convergence={props.offer.convergence}/>
                    )
                }
        </div>
    );
};

const Feature = props => {
    if ((props.feature.processes.length === 0 ||
        props.feature.processes.find(process => process === props.processType)) &&
        (!props.feature.clientContext || props.convergence === props.feature.clientContext)) {
        let innerHtml = {__html: props.feature.featureValues[0].value};
        const className = props.removeClassFromLast ? (props.length - 1 === props.index ? "" : props.className) : props.className;
//           return <p className={className} dangerouslySetInnerHTML={innerHtml}></p>
        return TooltipFromHtml.conditionalRenderWithClassName(props.feature.featureValues[0].value, className);
    } else {
        return null;
    }
};
